import React from 'react';
import Latex from 'react-latex';
export default function IndependenciaHomogeniedade(){ return(<>

<h2>
    Testes de Independência e Homogeniedade
</h2>

O objetivo é testar se há independência entre algum par de categorias A e B de um conjunto de <Latex>{"${A}_{k}$"}</Latex> e <Latex>{"${B}_{k}$"}</Latex> categorias.

<Latex displayMode={true}>{'$$'+
'\\begin{array}{c|c|c|c|c} '+
'&{B}_{1}&\\dots&{B}_{k2}&Total \\\\'+
'\\hline'+
'{A}_{1}&{p}_{11}&\\dots&{p}_{1k_2}&{P}_{A_1}\\\\[.3em] '+
'\\vdots&\\vdots&\\ddots&\\vdots&\\vdots\\\\[.3em] '+
'{A}_{k1}&{p}_{k_11}&\\dots&{p}_{k_1k_2}&{P}_{A_{k1}} \\\\[.3em] '+
'\\hline '+
'Total&{P}_{B_1}&\\dots&{P}_{B_{k2}}&1 '+
'\\end{array}'+
'\\quad\\quad'+
'\\begin{array}{c|c|c|c|c} '+
'&{B}_{1}&\\dots&{B}_{k2}&Total \\\\'+
'\\hline'+
'{A}_{1}&{n}_{11}&\\dots&{n}_{1k_2}&{n}_{A_1}\\\\[.3em] '+
'\\vdots&\\vdots&\\ddots&\\vdots&\\vdots\\\\[.3em] '+
'{A}_{k1}&{n}_{k_11}&\\dots&{n}_{k_1k_2}&{n}_{A_{k1}} \\\\[.3em] '+
'\\hline '+
'Total&{n}_{B_1}&\\dots&{n}_{B_{k2}}&n '+
'\\end{array}'
+'$$'}</Latex>

Sendo que <Latex>{"$p_{ij}=\\frac{n_{ij}}{n}$"}</Latex>

<h3>
    Hipóteses do Teste
</h3>

<Latex displayMode={true}>{'$$'+
'\\begin{cases} '+
'h_0 : {p}_{ij} = {P}_{A_i}{P}_{B_j}\\\\'+
'h_1 : {p}_{ij} \\mathrel{\\char`≠} {P}_{A_i}{P}_{B_j}'+
'\\end{cases}'
+'$$'}</Latex>

<h3>
    Estatística do Teste
</h3>

Dado que <Latex>{"$O_{ij}=n_{ij}$"}</Latex> e <Latex>{"$E_{ij}=nP_{A_{i}}P_{B_{j}}$"}</Latex>

<Latex displayMode={true}>{'$$'+
'X='+
'\\sum^{k_{1}}_{i=1}\\sum^{k_{2}}_{j=1}\\frac{(O_{ij}-E_{ij})^{2}}{E_{ij}}'+
'\\sim \\chi^{2}_{(k_{1}-1),(k_{2}-2)}'
+'$$'}</Latex>


<h3>
    Diferença entre o Teste de Independência e o de Homogeniedade
</h3>

A diferença consiste no modo em que é feita a amostragem. No teste de indepedência, a frequência de cada categoria (<Latex>{"$A_{k_{1}},B_{k_{2}}$"}</Latex>) é aleatória, e no teste de homogeniedade a frequência de cada categoria é definida antes da coleta da amostra ou seja a aleatorização é feita dentro de cada categoria.





</>)}